body {
  font-family: "Noto Serif JP", serif;
  font-weight: 400;
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

.information__inner {
  background-color: #DDF0F1;
  padding: 5rem 0.9375rem 5.3125rem;
}

.information__title {
  padding-top: 1.5rem;
  padding-bottom: 0.25rem;
  font-size: 1.25rem;
  font-family: "Noto Sans JP", sans-serif;
  font-weight: 700;
  line-height: 1.5rem;
  border-bottom: solid 0.0625rem #408F95;
}

.information__text {
  padding-top: 0.5rem;
  letter-spacing: 0.000625rem;
}