@use "global" as *;

.information__inner{
  background-color: #DDF0F1;
  padding: rem(80) rem(15) rem(85);
}

.information__title{
  padding-top: rem(24);
  padding-bottom: rem(4);
  font-size: rem(20);
  font-family: $second-font-family;
  font-weight: 700;
  line-height: rem(24);
  border-bottom: solid rem(1) $blue;
}

.information__text{
  padding-top: rem(8);
  letter-spacing: rem(.01);
}